import { Remove } from "@mui/icons-material";
import {
  Box,
  Container,
  Fab,
  Switch,
  Tooltip,
  Typography,
} from "@mui/material";
import React from "react";
import { useDispatch } from "react-redux";
import { patchTodoComplete, removeTodo } from "../../redux/slices/todo";
import EditModal from "../EditModal";

export const Todo = ({ text, id, completed }) => {
  const dispatch = useDispatch();
  const [checked, setChecked] = React.useState(completed);

  const handleChange = () => {
    setChecked(!checked);
  };

  const onSubmit = async () => {
    try {
      const field = {
        id,
        completed: checked,
      };

      dispatch(patchTodoComplete(field));
    } catch (err) {
      console.log(err);
      alert("   ");
    }
  };
  const onSubmitRemove = async (id) => {
    try {
      dispatch(removeTodo(id));
    } catch (err) {
      console.log(err);
      alert("   ");
    }
  };

  return (
    <Container
      maxWidth="sm"
      sx={{
        marginTop: 3,
        backgroundColor: checked ? "#A5A5A5" : "#474A51",
        borderRadius: 5,
      }}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: 2,
        }}
      >
        <Typography
          textAlign={"justify"}
          variant="p"
          textTransform={"uppercase"}
          sx={{ color: "#ffff" }}
        >
          {text}
        </Typography>
        <Box
          sx={{ display: "flex", gap: 2, alignItems: "center", marginLeft: 5 }}
        >
          <Tooltip title={checked ? " " : "  "}>
            <Switch
              onClick={onSubmit}
              checked={checked}
              onChange={handleChange}
              inputProps={{ "aria-label": "controlled" }}
            />
          </Tooltip>
          {checked ? "" : <EditModal id={id} />}
          <Box sx={{ "& > :not(style)": { m: 1 } }}>
            <Fab
              onClick={() => onSubmitRemove(id)}
              size="small"
              color="secondary"
              aria-label="delete"
            >
              <Tooltip title={" "}>
                <Remove />
              </Tooltip>
            </Fab>
          </Box>
        </Box>
      </Box>
    </Container>
  );
};
